import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Function_romasterlab from '@/components/function/romasterlab'
import Function_andromaster from '@/components/function/andromaster'
import Function_vroot from '@/components/function/vroot'
import Function_irootm from '@/components/function/irootm'
import Function_supercleaner from '@/components/function/supercleaner'
import Function_appcool from '@/components/function/appcool'
import Function_onelocker from '@/components/function/onelocker'
import Function_romjd from '@/components/function/romjd'
import Function_wp8launcher from '@/components/function/wp8launcher'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/function/romasterlab',
      name: 'Function_romasterlab',
      component: Function_romasterlab
    },
    {
      path: '/function/andromaster',
      name: 'Function_andromaster',
      component: Function_andromaster
    },
    {
      path: '/function/supercleaner',
      name: 'Function_supercleaner',
      component: Function_supercleaner
    },
    {
      path: '/function/vroot',
      name: 'Function_vroot',
      component: Function_vroot
    },
    {
      path: '/function/irootm',
      name: 'Function_irootm',
      component: Function_irootm
    },
    {
      path: '/function/appcool',
      name: 'Function_appcool',
      component: Function_appcool
    },
    {
      path: '/function/onelocker',
      name: 'Function_onelocker',
      component: Function_onelocker
    },
    {
      path: '/function/wp8launcher',
      name: 'Function_wp8launcher',
      component: Function_wp8launcher
    },
    {
      path: '/function/romjd',
      name: 'Function_romjd',
      component: Function_romjd
    }
  ]
})



